.Modal {
  max-width: 768px;
  width: 100%;
  margin: auto;
  position: fixed;
  left: 0;
  right: 0;
  top: 55px;
  background:  white;
  padding:0 10px 10px 10px;
  border-radius: 20px;
  -webkit-box-shadow: 0 0 10px rgba(0,0,0,.4);
          box-shadow: 0 0 10px rgba(0,0,0,.4);
}

.Modal-close {
  width: 50px;
  height: 50px;
  position: absolute;
  right: -25px;
  top: -25px;
  border-radius: 50%;
  border: none;
  background: #171c30;
  color: white;
  cursor: pointer;
}

.Modal-close:after {
  content: "X";
  font-weight: bold;
  font-size: 30px;
}
button{
  cursor: pointer;
}
.Cerrar{
  border: none;
  background: transparent;
  color: gray;
  font-size: 10px;
}
.Cerrar:hover{
  color: black;
  font-weight: bold;
}



