.Spinner {
  position: absolute;
  top: 0;
  bottom:0;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  left: 0;
  right: 0;
  display: -ms-flexbox;
  display: flex;
  background: rgba(0,0,0,0.5);
}

.Spinner span {
  color: white;
  font-size: 20px;
}



